.footer {
    width: 100%;
    background-color: #f5f5f5;
    height: 50px;
    position: relative;
    bottom: -35px;

    display: flex;
    flex-direction: row;
    justify-content: center;

    p {
      text-align: center;
      color: #3a3a3f;
    }
    .icon {
      width: 50px;
      padding-top: 13px;
      cursor: pointer;
    }
    .first {
      margin-left: 25px;
    }
  }

  @media screen and (max-width: 479px) {
    .footer {
      height: 65px;
      p {
        padding: 4px 0;
        text-align: center;
        color: #3a3a3f;
      }
      .icon {
        padding-top: 18px;
      }
    }
  }